$grid: 8px;

.item {
  user-select: none;
  padding: $grid * 2;
  margin: 0;
  margin-bottom: $grid;
  background: white;
  border: 1px solid #ddd;
  border-left-width: 5px;
  display: flex;
  align-items: center;

  > p {
    padding: 0;
    margin: 0;
    margin-right: 10px;
    flex: 1;
    text-align: center;
  }

  > svg {
    color: #ccc;
    margin-right: 5px;
  }
}

.item:last-child {
  margin-bottom: 0;
}

.item.isDragging {
  border-color: #999;
}

.item.isCorrect {
  color: #21ba45;
  border-color: #21ba45;
  background-color: lighten($color: #21ba45, $amount: 50);
}

.item.isIncorrect {
  color: #dc3545;
  border-color: #dc3545;
  background-color: lighten($color: #dc3545, $amount: 40);
}

