.root {
  //   border: 80px solid #c40000;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: Georgia, serif;
  padding: 80px;
  background-color: #ddd;

  .inner {
    flex: inherit;
    display: inherit;
    flex-direction: inherit;
    justify-content: inherit;
    align-items: inherit;
    background-color: #fff;
    width: 100%;
    padding: 40px;
    font-family: inherit;

    h2 {
      border-bottom: 1px solid #ccc;
      padding-bottom: 20px;
      text-align: center;
      font-size: 2vw;
      font-family: inherit;
      letter-spacing: -0.1vw;
    }

    h1 {
      font-size: 4vw;
      margin-bottom: 0;
      text-align: center;
      white-space: break-spaces;
      font-family: inherit;
      letter-spacing: -0.1vw;
    }

    .main,
    .bottom {
      display: inherit;
      flex-direction: inherit;
      justify-content: inherit;
      align-items: inherit;

      p {
        text-align: center;
      }
    }

    .description {
      font-size: 1.5vw;
      // to render line breaks (\n)
      white-space: pre-line;
    }

    .signature {
      font-family: 'Brush Script MT', cursive;
      font-size: 2.5vw;
      border-bottom: 1px solid #ccc;
      min-width: 25vw;
      margin-bottom: 0;
      color: rgb(0, 0, 150);
      line-height: 1.6vw;
    }

    .instructor {
      margin-top: 10px;
      font-style: italic;
      font-size: 1.25vw;
    }

    .instructorName {
      font-weight: bold;
    }

    .logo {
      height: 7vw;
      width: 15vw;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

@media print {
  .root {
    -webkit-print-color-adjust: exact;
  }
}
